html {
  font-size: 62.5%;
  box-sizing: border-box;
  background: #000000;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}

.img-fondo {
  background-image: url(../build/4.png);
}

p {
