  font-family: "Open Sans", sans-serif;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  font-family: "Changa One", cursive;
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 3rem;
}

img {
  width: 100%;
}

