.contenido_header {
  padding-top: 1rem;
}

.navegador_principal {
  height: 8rem;
  padding: 0 2rem;
  justify-content: center;
  display: block;
  overflow: hidden;
  transition: all 1s ease;
}
@media (min-width: 992px) {
  .navegador_principal {
    display: grid;
    grid-template-columns: 1fr 3fr 1fr;
  }
}
.navegador_principal div {
  display: flex;
  align-items: center;
  text-align: center;
}
.navegador_principal div img {
  max-width: 8rem;
}

.size {
  height: 25rem;
}
@media (min-width: 992px) {
  .size {
    height: 8rem;
  }
}

.navegador_principal-logo {
  justify-content: left;
  width: 100%;
}
@media (min-width: 992px) {
  .navegador_principal-logo {
    justify-content: left;
  }
}

.navegador_principal-secciones {
  margin: 0;
  flex-direction: column;
  justify-content: center;
  transition: all 0.5s ease-in-out;
}
@media (min-width: 992px) {
  .navegador_principal-secciones {
    flex-direction: row;
    justify-content: space-evenly;
  }
}
.navegador_principal-secciones a {
  color: white;
  text-align: justify;
  text-decoration: none;
  font-size: 2.2rem;
  text-transform: uppercase;
  margin: 1rem auto;
}
.navegador_principal-secciones a:hover {
  color: #f5a226;
}

.navegador_principal-redes {
  margin: 0;
  justify-content: center;
  gap: 2rem;
  width: 100%;
  transition: all 0.5s ease-in-out;
}
@media (min-width: 992px) {
  .navegador_principal-redes {
    position: relative;
    justify-content: flex-end;
  }
}
.navegador_principal-redes img {
  width: 6rem;
}

.icon {
  position: absolute;
  right: 2rem;
  top: 2.5rem;
  display: block;
}
@media (min-width: 992px) {
  .icon {
    display: none;
  }
}

.icon:hover {
  cursor: pointer;
}

.img-fondo {
  position: relative;
  background-image: url(../img/4.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 100% 100%;
}

.kits {
  padding: 5rem 0rem 15rem 0;
}
.kits h1 {
  text-transform: uppercase;
  margin: 0;
  text-align: center;
  color: black;
  font-size: 3.5rem;
}
@media (min-width: 480px) {
  .kits h1 {
    font-size: 5rem;
  }
}
.kits h1 span {
  font-size: 3rem;
  color: white;
}
@media (min-width: 480px) {
  .kits h1 span {
    font-size: 5rem;
  }
}

.container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 10rem 5rem;
  padding: 10rem 5rem;
}
.container .card {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  width: 30rem;
  height: 25rem;
  background: white;
  border-radius: 2rem;
  box-shadow: 0 3.5rem 8rem rgba(0, 0, 0, 0.15);
  transition: 0.5s;
}
@media (min-width: 480px) {
  .container .card {
    width: 40rem;
    height: 35rem;
  }
}
.container .card:hover {
  height: 27rem;
}
@media (min-width: 480px) {
  .container .card:hover {
    height: 40rem;
  }
}
.container .card:hover .imgBx {
  top: -5rem;
  scale: 0.9;
  box-shadow: 0 1.5rem 4.5rem rgba(0, 0, 0, 0.2);
}
@media (min-width: 480px) {
  .container .card:hover .imgBx {
    top: -7rem;
  }
}
.container .card:hover .content {
  top: 12.5rem;
  height: 15rem;
}
@media (min-width: 480px) {
  .container .card:hover .content {
    top: 22rem;
    height: 15rem;
  }
}
.container .card .imgBx {
  position: absolute;
  top: 2rem;
  width: 25rem;
  height: 15rem;
  background: #333;
  border-radius: 1.2rem;
  overflow: hidden;
  transition: 0.5s;
}
@media (min-width: 480px) {
  .container .card .imgBx {
    width: 35rem;
    height: 25rem;
  }
}
.container .card .imgBx img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.container .card .content {
  position: absolute;
  top: 20rem;
  width: 100%;
  padding: 0 30px;
  height: 4rem;
  overflow: hidden;
  text-align: center;
  transition: 0.5s;
}
@media (min-width: 480px) {
  .container .card .content {
    top: 28.5rem;
  }
}
.container .card .content h2 {
  font-weight: 700;
  color: black;
  margin: 0;
  font-size: 2.5rem;
}
@media (min-width: 480px) {
  .container .card .content h2 {
    font-size: 3.5rem;
  }
}
.container .card .content a {
  font-size: 2rem;
  color: white;
  position: relative;
  top: 3rem;
  display: inline-block;
  padding: 1rem 2rem;
  background-color: #47c2e7;
  font-weight: 500;
  text-decoration: none;
  border-radius: 1rem;
}
@media (min-width: 480px) {
  .container .card .content a {
    padding: 1.2rem 2.5rem;
  }
}

