@use "base/mixins" as m;
.galeria{
    width: 100%;
    height: 90rem;
    display: flex;
    justify-content: center;
    flex-direction: column;
    background-size: cover;
    background-position: 0% 100%;
    animation: animate 30s infinite alternate ease-in-out;

    img{
        max-width: 30rem;
        margin: 0 auto;
    }
    h1{
        text-transform: uppercase;
        color: white;
        margin: 0 auto;
        font-size: 3.5rem;
        text-align: center;
    }
    @include m.tablet{
        h1{
            font-size: 4rem;
            margin: 0 auto;
        }
    }
    @include m.desktop{
        h1{
            font-size: 5rem;
            margin: 0 0 0 8rem;
            text-align: start;
        }
        img{
            margin-left: 6rem;
        }
    }
}
@keyframes animate{
    0%{
        background-image:url('../img/pic1.webp');
    }

    25%{
        background-image:url('../img/pic2.webp');
    }

    50%{
        background-image:url('../img/pic3.webp');
    }
    75%{
        background-image:url('../img/pic4.webp');
    }
    100%{
        background-image:url('../img/pic1.webp');
    }
}

