@use"base/variables" as v;
@use"base/mixins" as m;
.nosotros{
    padding: 5rem 2rem 1rem 2rem;
    text-align: center;
    
    h1{
        margin: 0;
        font-size: 4rem;
        text-transform: uppercase;
        @include m.telefono{
            font-size: 6rem;
        }

    }
    .nosotros_info{
        margin: 5rem  0 0  0;
        padding: 0 ;
        display: grid;
        grid-template-rows: .1fr .2fr;
        gap: 5rem;
        justify-content: center;
        align-items:center;
        text-align: center;
        @include m.tablet{
            padding: 0 5rem;
            margin: 5rem  0 0  0;
            gap: 15rem;
        }
        @include m.desktop{
            padding: 0 10rem;
        }
        @include m.extra{
            grid-template-columns: 1.5fr 2fr;
            align-items:center;
            padding: 0 20rem;
            gap: 5rem;
        }

        .nosotros_info-img{
            img{
                max-width: 50rem;
                max-height: 70rem;
                border-radius: 3rem;
                box-shadow: -17px 17px 4px 6px rgba(61, 61, 61, 0.247);
                @include m.desktop{
                    max-width: 60rem;
                }
            }
        }
        .nosotros_info-text{
            h3{
                letter-spacing: .2rem;
                font-size: 2rem ;
                line-height: 5rem;
                text-align: center;
                color: white;
                text-transform: uppercase;
                font-weight: 900;
                @include m.telefono{
                    font-size: 3rem ;
                }
                @include m.desktop{
                    font-size: 4rem ;
                }
            }
            p{
                line-height: 4rem;
                text-align: justify;
                font-size: 1.5rem;
                font-weight:600;
                margin: 0 auto;
                @include m.telefono{
                    font-size: 2rem ;
                }
                @include m.desktop{
                    font-size: 2.5rem ;
                }
                span{
                    color: #ffffff;
                }
            }
        }
    }
}
