@use"base/variables" as v;
@use"base/mixins" as m;
.estadisticas{
    padding: 10rem 0 10rem 0;
    text-align: center;
    text-transform: uppercase;
    h1{
        font-size: 3.5rem;
        margin: 0;
        color: black;
        @include m.telefono{
            font-size: 6rem;
        }
    }
    h3{
        margin-top: 2rem;
        font-size: 3rem;
        color: #ffffff;
    }
.estadisticas_redes{
    margin: 0 auto;
    position: relative;
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    gap: 10rem;
    @include m.tablet{
        gap: 20rem;
   }
    @include m.extra{
         flex-direction: row;
    }

}
.estadisticas_tiktok{
    height: 55rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 40rem;
    border-radius: 4rem;
    @include m.tablet{
        font-size: 7rem;
        width: 60rem;
        height: 65rem;
   }
}
    .titulo_tiktok{
        margin: 0 2rem;
        letter-spacing: 10px;

    }
    p{
        z-index: 1;
        letter-spacing: 2px;
        margin: 2rem auto;
        font-weight: 900;
        color: white;
        font-size: 2.5rem;
        @include m.tablet{
            font-size: 3.5rem;
       }
    }
}
.estadisticas_instagram{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 4rem;
    height: 55rem;
    width: 40rem;
    background-size: cover;
    border-radius: 4rem;
    @include m.tablet{
        font-size: 7rem;
        width: 60rem;
        height: 65rem;
   }
    p{
        z-index: 1;
        letter-spacing: 5px;
        margin: 2rem auto;
        font-weight: 900;
        color: white;
        font-size: 2.5rem;
        @include m.tablet{
            font-size: 3.5rem;
       }
    }
}
.filter{

    border-radius: 4rem;
    z-index: 0;
    width: 40rem;
    height: 45rem;
    position: absolute;
    background: linear-gradient(135deg, rgba(255, 255, 255, 0.062),rgba(255, 255, 255, 0.062));
    backdrop-filter:blur(5px);
    -webkit-backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.18);
    box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.37);
    @include m.tablet{
        width: 60rem;
        height: 55rem;
   }
}
.estadisticas_logo{
    z-index: 1;
    text-align: center;
    display: flex;
    color: black;
    justify-content: center;
    align-items: center;
    img{
        width: 8rem;
        @include m.tablet{
            width: 10rem;
       }
    }
    p{
        margin: 0;
        text-align: center;
        font-size: 4rem;
        color: black;
        @include m.tablet{
            font-size: 7rem;
       }
    }
}
.estadisticas_datos{
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 2rem;
    z-index: 1;
    p{
        letter-spacing: 0;
        margin: 1rem auto;
        font-weight: 700;
        font-size: 1.5rem;
        color: black;
        @include m.tablet{
            font-size: 2.5rem;
       }
    }
    span{
        font-size: 2rem;
        color: gold;
        @include m.tablet{
            font-size: 3rem;
        }
        
    }
}
.estadisticas_segidores{
    display: flex;
    justify-content: center;
    gap: 10rem;
    div{
        p{
            font-size: 5rem;
            font-weight: 700;
        }
    }
}