@use"base/mixins" as m;
.descripcionKits{
    padding: 1rem 0;
    background-image: url(../img/imagennosotros.webp);
    background-position: 75% 50%;
  
    height: 90rem;
    background-size: cover;
    @include m.desktop{
        background-position: 10% 50%;
    }
}

.descripcionKits_Tarjeta{
    margin: 2rem auto;
    border-radius: 4rem;
    z-index: 0;
    width: 38rem;
    height: 80rem;
    background: linear-gradient(135deg, rgba(94, 94, 94, 0.404),rgba(95, 95, 95, 0.418));
    backdrop-filter:blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.18);
    box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.37);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    @include m.telefono{
        width: 46rem;
        height: 80rem;
    }
    @include m.tablet{
        width: 50rem;
        height: 80rem;
    }
    @include m.tablet{
        width: 70rem;
        height: 80rem;
    }

    @include m.desktop{
        margin: 2rem 6rem;
    }
}
.descripcionKits_titulo{
    text-align: center;
    text-transform: uppercase;
    h1{
        font-size: 3rem;
        margin: .5rem auto;
        @include m.telefono{
            margin: 1.5rem auto;
            font-size: 4rem;
        }
        @include m.tablet{
            margin: 1.7rem auto;
            font-size: 6rem;
        }

    }
}
.descripcionKits_info{
    margin: 0 3rem;
    p{
        font-size: 1.7rem;
        font-weight: 600;
        color: white;
    }
    ul{
        list-style:disc;
        margin: 0 1rem;
        padding: 0;
        @include m.tablet{
            margin: 0 2rem;
        }
    }
    li{
        list-style:disc;
        color: white;
        text-align: justify;
        margin-top: 1rem;
        font-size: 1.5rem;
        @include m.tablet{
            font-size: 2rem;
        }
    }
}
.descripcionKits_piesDePagina{
    padding: 1rem;
    margin: 0 2rem;
    p{ 
    text-align: justify;
    color: white;
    font-size: 1.3rem;
    justify-content: center;
    }
}
.descripcionKits_boton{
    display: flex;
        a{
            color: black;
            width: 27rem;
            padding: 1rem;
            margin: 1rem auto .5rem auto;
            border-radius: 4rem;
            border: none;
            background-color: #47c2e7;
            @include m.telefono{
                margin: 3rem auto 1rem auto;
                padding: 1.5rem;
            }
            &:hover{
                cursor: pointer;
             }
        }
}