footer{
    padding: 2rem 0rem;
    p{
        color: white;
        text-align: center;
        font-size: 2rem;
        font-weight: 500;
        margin: 0;
    }
    h2{
        font-size: 2rem;
        margin:2rem 0rem 0rem 4rem ;
        color: white;
        text-transform: uppercase;
    }
    ul{
        margin:2rem 0rem 0rem 2rem;
        list-style: none;
        text-decoration: none;
        color: white;
        li{
            display: flex;
            text-align: center;
            align-items: center;
            gap: 1rem;
            font-size: 2rem;
            margin-top: 2rem;
            img{
                max-width: 3rem;
            }
        }
    }
}
