@use "variables" as v;
@use "mixins" as m; 

html{
    font-size: 62.5%;
    box-sizing: border-box;
    background: v.$negro;
}
*,
*:before,
*:after{
    box-sizing: inherit;
}
body{
    font-family: v.$fuenteSecundaria;
    font-size: 1.6rem;
    &.fijar-body{
        overflow: hidden;
    }
}
.img-fondo{
    background-image: url(../build/4.png);
}
p{
    font-size: 2rem;
    font-family: v.$fuenteSecundaria;
}
.contenedor{
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}
a{
    text-decoration: none;
}
h1,h2,h3{
    font-family: v.$fuentePrincipal;
    margin:0 0 5rem 0;
}
h1{
    font-size: 6rem;
    
}
h2{
    font-size: 3rem;
}
h3{
    font-size: 3rem;
}
img{
    width: 100%;
}
