@use"base/variables" as v;
@use"base/mixins" as m;
.recomendaciones{
    padding: 8rem 0;
    background-color: v.$naranja;
    h1{
        margin: 0;
        text-align: center;
        text-transform: uppercase;
        font-size: 3rem;
        color: rgb(255, 255, 255);
        @include m.tablet{
            font-size: 4rem;
        }
        @include m.desktop{
            font-size: 5.5rem;
        }

    }
    h2{
        margin : 2rem auto;
        text-align: center;
        color: rgb(0, 0, 0);
        font-size: 2.5rem;
        @include m.tablet{
            font-size: 3rem;
        }
        @include m.desktop{
            font-size: 3.5rem;
        }
    }
}
#map{
    max-width: 100%;
    height: 60rem;
    margin: 0 auto;
    @include m.tablet{
        max-width: 90%;
        height: 70rem;
    }
    @include m.desktop{
        max-width: 80%;
    }
}
.leaflet-popup-content{
    width: 25rem;
    h1{
        font-size: 2rem;
        color: black;
        font-weight: 200;
        margin: 0;
        text-align: center;
        @include m.desktop{
            font-size: 2.5rem;
        }
    }
    h2{
        font-size: 1.7rem;
        margin: 0;
        font-family: v.$fuenteSecundaria;
    }
    h3{
        font-size: 1.5rem;
        margin: 0;
        text-align: center;
        font-family: v.$fuenteSecundaria;
    }
}
.leaflet-control-layers-overlays{
    font-size: 1.5rem;
    h1{
        color: black;
        font-size: 2rem;
        font-weight: 200;
    }
}

.pruebaa{
    z-index: 800;
}